.Title {
    box-sizing: border-box;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 1em;
    margin-bottom: 2em;
    background-color: rgb(0, 26, 255);
    color: #fff;
    font-family: arial;

  }
  .Title-Subtitle {
    color: rgba(151, 248, 255, 0.75);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .Title a {
    display: inline-block;
    padding: 0.5em 1em;
    color: rgba(255, 255, 255, 0.835);
    font-weight: bold;
    text-decoration: none;
  }

  .Title .nav-link-active {
    color: #fff;
    border-bottom: 2px solid;
  }